<template>
  <div class="page-view" id="account">
    <navbar :title="'个人资料'" ></navbar>
    <div class="user-header">
      <yd-flexbox  direction="vertical">
        <yd-flexbox-item>
          <img src="../../assets/1.png" alt="">
          <p>
            哈哈哈
          </p>
        </yd-flexbox-item>
      </yd-flexbox>
    </div>
    <div class="account-body">
      <component :is="view" @step1="step1" @step2="step2"></component>
    </div>
  </div>
</template>

<script>
  import Navbar from '../../components/Navbar.vue'
  import account1 from './templates/Account1.vue'
  import account2 from './templates/Account2.vue'
  import account3 from './templates/Account3.vue'
    export default{
      components:{
        Navbar,
        account1,
        account2,
        account3
      },
      data(){
        return {
          view:'account1',
          newAccount:{
            phone:'',
            code:'',
          },
          existAccount:{
            phone:'',
            code:'',
          }
        }
      },
      created(){
      },
      methods:{
        step1(){
          this.view='account2'
        },
        step2(){
          this.view='account3'
        }
      }
    }
</script>

<style lang="less" rel="stylesheet/less">
  @import "../../style/config.less";
#account{
  .tab-nav-item,
  .tab-nav-item.tab-active,
  .tab-nav-item.tab-active:active{
    background-color:transparent ;
    color:@skin-color;
  }
  .account-body{
    padding: 0 50px;
  }
  .account-btn{
    padding-top: 30px;
    text-align: center;
  }
  .tab-panel-item{
    padding-top: 20px;
  }
  .flexbox-item{
    padding: 10px 0;
  }
  .m-input{
    padding: 8px 0;
    border-bottom:1px solid #000;
  }
  .tab-nav:after{
    border-bottom: none;
  }
  .btn-primary{
    background-color: @skin-color;
  }
  p{
    line-height: 2;
    text-align: center;
  }
}
</style>



