<template>
  <div>
    <p>您将绑定手机号123213123</p>
    <p>请设置用户名和密码</p>
    <yd-flexbox>
      <yd-flexbox-item>
        <yd-input class="username" max="20" v-model="username"
                   placeholder="用户名"></yd-input>
      </yd-flexbox-item>
    </yd-flexbox>
    <yd-flexbox>
      <yd-flexbox-item>
        <yd-input  max="20" v-model="password"
                   placeholder="输入密码"></yd-input>
      </yd-flexbox-item>
    </yd-flexbox>
    <yd-flexbox>
      <yd-flexbox-item>
        <yd-input  max="20" v-model="repassword"
                   placeholder="确认密码"></yd-input>
      </yd-flexbox-item>
    </yd-flexbox>
    <div class="account-btn">
      <yd-button type="primary" @click.native="submit">确认绑定</yd-button>
    </div>
  </div>
</template>

<script>
  export default{
    name:'account2',
    data(){
      return {
        username:'',
        password:'',
        repassword:''
      }
    },
    methods:{
      submit(){
        this.$emit('step2')
      }
    }

  }
</script>

<style scoped lang="less">
  .username{
    position: relative;
    &:after{
       content:'用户名设置后不可更改';
       display: block;
        position: absolute;
       right:0;
       bottom:-16px;
       font-size: 10px;
     }
  }
</style>



